/*Hgroup*/
h1, h2, h3, h4, p {
  font-family: 'Marcellus SC', serif;
  color: #2a2a2a;
}
h6 {
  font-weight: 500;
}
span {
  font-weight: normal;
}
p {
  font-family: 'Marcellus SC', serif;;
  font-size:120%;
  line-height:1.2;
  font-weight: bold;
  color: #2a2a2a;
}
header {
  margin-bottom: 35px;
}
#img {
  width: 100%;
  z-index: 0;
  margin-top: 35px;
}
.img {
  height: 100%;
  width: 100%;
}

/*HERO*/
.hero {
margin-top: 129px;
}
.nav-wrapper {
  padding-bottom: 35px;
  margin-bottom: 0;
  height: 230px;
  border-bottom: solid #4b5320  6px;
}
.navbar{
  margin-bottom: 45px;
}
.nav a i {
  color: #4b5320;;
}
.nav {
  color: #4b5320;
}
.reg-btn {
  margin-top: 35px;
  margin-bottom: 40px;
}
.logo-png {
  padding-left: 47px;
  padding-bottom: 35px;
}
.card .card-reveal {
  background-color: #F2F2F2;
}
.card {
  background-color: #F2F2F2;
}
.btn {
  margin-top: 20px;
  margin-left: 5%;
  font-family: serif;
}
.shadow {
  -moz-box-shadow:    inset 0 0 20px #000000;
  -webkit-box-shadow: inset 0 0 20px #000000;
  box-shadow:         inset 0 0 20px #000000;
}
